.ListaTareasSeccion {
  display: flex;
  justify-content: space-around;
  cursor: pointer;
  padding: 1rem;
  width: 100%;
  text-align: center;
  transition: all 0.2s ease-in-out;
  height: min-content;
  border-bottom: 2px solid #bfc0c0;
  background-color: #2d3142;
}
.ListaTareasSeccion h1 {
  margin: 0;
  font-size: 1.5rem;
}
.ListaTareasSeccion.activo {
  background-color: #4f5d75;
  border-bottom: 2px solid #ffffff;
}
.ListaTareasSeccion:hover {
  background-color: #4f5d75;
  border-bottom: 2px solid #ef8354;
}
@media only screen and (max-width: 400px) {
  .ListaTareasSeccion {
    flex-direction: column !important;
    margin: 0 !important;
    padding: 0 !important;
    border: none;
    border-left: 2px solid #bfc0c0;
  }
  .ListaTareasSeccion.activo {
    border: none;
    border-left: 2px solid #ffffff;
  }
  .ListaTareasSeccion:hover {
    border: none;
    border-left: 2px solid #ef8354;
  }
}
